.menu {
    &--nav {
        padding-right: 30px;
        padding-left: 31px;

        @media screen and (max-width: 1024px) {
            padding-right: 0;
            padding-left: 0;
        }

        @media screen and (max-width: 579px) {
            border-right: 2px solid $dimWhite;
            cursor: pointer;
        }
    }

    &--footer {
        padding-right: 30px;
        padding-left: 12px;

        @media screen and (max-width: 1024px) {
            padding-left: 0;
            padding-right: 20px;
        }

        @media screen and (max-width: 579px) {
            padding-right: 0;
            margin-bottom: 15px;
            margin-top: 10px;
        }
    }

    &--cat {
        @media screen and (max-width: 579px) {
            width: 50%;
        }
    }

    &--filter {
        @media screen and (max-width: 579px) {
            display: none;
        }
    }

    &__link {
        padding: 5px;

        &--nav {
            margin-right: 24px;

            @media screen and (max-width: 1024px) {
                display: none;
            }

            &:last-child {
                margin-right: 0;
            }
        }

        &--footer {
            margin-right: 50px;

            @media screen and (max-width: 1024px) {
                margin-right: 35px;
            }

            @media screen and (max-width: 720px) {
                margin-right: 20px;
            }

            &:first-child {
                margin-right: 34px;

                @media screen and (max-width: 1024px) {
                    margin-right: 25px;
                }

                @media screen and (max-width: 720px) {
                    margin-right: 20px;
                }
            }

            &:last-child {
                margin-right: 0;
            }
        }

        &--sub {
            padding-right: 20px;

            &:after {
                content: '';
                position: absolute;
                top: 50%;
                right: 0;
                width: 10px;
                height: 6px;
                transform: translateY(-50%);
                background: url("../img/icons/arrow.png") no-repeat;
                background-size: cover;
            }
        }
    }
}
